<template>
  <div>
    <div>
      <div class="page-title-cont">
        <h1 class="wp-heading-inline">WPCal.io - {{ T__("Event Types") }}</h1>
        <router-link to="/event-type/add" class="page-title-action">{{
          T__("+ Create New Event Type")
        }}</router-link>
        <PremiumInfoBadge style="margin: -2px 0 0 10px;">
          <em>Free: 1 active event type<br />Plus: Unlimited</em>
        </PremiumInfoBadge>
        <AdminHeaderRight />
      </div>
      <div class="service-list-cont">
        <div
          class="service-list-single"
          :class="{
            ['clr-' + service.color]: true,
            inactive: service.status == -1
          }"
          v-for="(service, index) in service_list_final"
          :key="index"
        >
          <h4>
            <a :href="'#/event-type/edit/' + service.id">{{ service.name }}</a>
          </h4>
          <div class="et-edit-link">
            <a
              :href="'#/event-type/edit/' + service.id"
              @click="toggle_service_menu(service.id)"
              >{{ T__("EDIT") }}</a
            >
          </div>
          <span
            :class="{
              ['service_overflow_options_' + service.id]: true
            }"
          >
            <a
              class="overflow-btn"
              style="float:right"
              @click="toggle_service_menu(service.id)"
            ></a>
            <span
              class="overflow-options"
              :class="{
                hidden: menu_open_service_id != service.id
              }"
            >
              <a
                class="menu-item"
                style="color: rgb(232, 70, 83);"
                @click="may_show_service_status_change_dialog(-2, service)"
                >{{ T__("Delete this Event Type") }}</a
              >
              <hr />
              <div class="menu-item" style="display:flex">
                <span style="color: #7c7d9c !important; font-size: 13px;">{{
                  T__("On/Off")
                }}</span>
                <OnOffSlider
                  style="margin-left:auto;"
                  v-if="service.status == 1 || service.status == -1"
                  :name="service.id"
                  v-model="service.status"
                  true_value="1"
                  false_value="-1"
                  @prop-slider-value-changed="
                    may_show_service_status_change_dialog($event, service)
                  "
                />
              </div>
            </span>
          </span>

          <div class="et-meta">
            {{ service.duration | mins_to_str }},
            {{ service.relationship_type | relationship_type_str }}
          </div>
          <div
            v-if="service.post_details.link"
            style="display:flex; margin-top:auto;"
          >
            <a
              class="view-booking-page"
              :href="service.post_details.link"
              target="_blank"
              >{{ T__("View Booking Page") }}</a
            >
            <div style="margin-left:auto;">
              <img
                :src="$store.getters.get_current_admin_details.profile_picture"
                width="30"
                style="border-radius:50%;"
                :title="
                  'This event type is available only for ' +
                    $store.getters.get_current_admin_details.display_name +
                    '.'
                "
              />
            </div>
          </div>
        </div>
      </div>
    </div>
    <v-dialog />
    <AlertAdminToUpdateProfile
      :service_details="sample_real_service_details"
      v-if="sample_real_service_details && sample_real_service_details.name"
    />
  </div>
</template>

<script>
import OnOffSlider from "@/components/form_elements/OnOffSlider.vue";
import AlertAdminToUpdateProfile from "@/components/admin/AlertAdminToUpdateProfile.vue";
import { admin_service_status_toggle_mixin } from "@/utils/admin_service_status_toggle_mixin.js";

import axios from "axios";

export default {
  name: "ServiceList",
  mixins: [admin_service_status_toggle_mixin],
  components: {
    OnOffSlider,
    AlertAdminToUpdateProfile
  },
  data() {
    return {
      document_title: this.T__("Event Types"),
      service_list: {},
      menu_open_service_id: null
    };
  },
  computed: {
    service_list_final() {
      if (!Array.isArray(this.service_list) || !this.service_list.length) {
        return [];
      }
      let service_list_tmp = this.clone_deep(this.service_list);
      service_list_tmp.sort((a, b) => b.id - a.id); //latest first - decending
      return service_list_tmp;
    },
    sample_real_service_details() {
      let service_details = null;
      if (
        Array.isArray(this.service_list_final) &&
        this.service_list_final.length
      ) {
        for (let service_key in this.service_list_final) {
          if (this.service_list_final[service_key].status == "1") {
            service_details = this.clone_deep(
              this.service_list_final[service_key]
            );
            break;
          }
        }
        if (!service_details) {
          //If non of the service is active
          service_details = this.clone_deep(this.service_list_final[0]);
        }
      }
      return service_details;
    }
  },
  methods: {
    load_page() {
      let action = "get_services_of_current_admin";
      let wpcal_request = {};
      wpcal_request[action] = {
        dummy__: ""
      };

      let post_data = {
        action: "wpcal_process_admin_ajax_request",
        wpcal_request: wpcal_request
      };

      axios
        .post(window.wpcal_ajax.ajax_url, post_data)
        .then(response => {
          //console.log(response);
          if (
            response.data &&
            response.data[action].hasOwnProperty("service_list")
          ) {
            this.service_list = response.data[action].service_list;
          }
        })
        .catch(error => {
          console.log(error);
        });
    },
    toggle_service_menu(service_id) {
      this.menu_open_service_id =
        this.menu_open_service_id == service_id ? null : service_id;
      if (this.menu_open_service_id) {
        setTimeout(() => {
          this.on_click_outside(
            ".service_overflow_options_" + service_id,
            () => {
              if (this.menu_open_service_id == service_id) {
                this.menu_open_service_id = null;
              }
            }
          );
        }, 100);
      }
    }
  },
  mounted() {
    this.load_page();
  }
};
</script>
