        <div class="form-row" v-if="view == 'add'">
          <label for="form_unique_link" style="cursor:default;">
            {{ T__("Event type booking page") }}
          </label>
          <span class="txt-help">
            {{
              T__(
                "A page will be created and the booking widget will be added to it. You can manage it after creating this event type."
              )
            }}
          </span>
        </div>
        <!-- <form-row :validator="$v.form.locations">
          <label for="form_locations">
            Location
            <em>optional</em>
          </label>
          <input id="form_locations" type="text" v-model="form.locations" />
          <template v-slot:after>
