          </template>
        </form-row> -->
        <form-row>
          <label>
            {{ T__("Location (online &amp; offline)") }}
            <em>{{ T__("optional") }}</em>
          </label>
          <span class="txt-help">
            {{
              T__(
                "Use this to specify how and where both parties will connect at the scheduled time. This will be displayed in the booking screen."
              )
            }}
          </span>
          <AdminLocationSelector v-model="form.locations" />
        </form-row>
        <form-row :validator="$v.form.descr">
          <label for="form_descr">
            {{ T__("Description/Instructions") }}
            <em>{{ T__("optional") }}</em>
          </label>
          <span class="txt-help">
            {{
              T__(
                "Use this to provide a short description of your event. This will be displayed in the booking screen."
              )
            }}
          </span>
          <textarea id="form_descr" v-model="form.descr"></textarea>
        </form-row>
        <!-- <div class="form-row">
          <label for="form_unique_link">
            Event type booking page
            <abbr class="required" title="required">*</abbr>
            <em>required</em>
          </label>
