          </button>
          <button
            v-else-if="view == 'edit'"
            type="button"
            class="wpc-btn primary lg"
            :disabled="!create_or_update_btn_is_enabled"
            @click="$emit('submit-form', 2)"
          >
            {{ T__("Save") }}
          </button>
          <button
            v-if="view == 'edit'"
            type="button"
