<script>
import ColorSelector from "@/components/form_elements/ColorSelector.vue";
import AdminLocationSelector from "@/components/form_elements/AdminLocationSelector.vue";

export default {
  name: "ServiceFormStep2",
  components: {
    ColorSelector,
    AdminLocationSelector
  },
  props: {
    view: {
      type: String
    },
    step: {
      type: Object,
      required: true
    },
    form: {
      type: Object,
      required: true
    },
    create_or_update_btn_is_enabled: {},
    $v: {}
  },
  data() {
    return {};
  },
  methods: {
    auto_focus_form_element() {
      if (this.view == "add" && !this.form.name && this.$refs.service_name) {
        setTimeout(() => {
          this.$refs.service_name.focus();
        }, 50);
      }
    }
  },
  watch: {
    "step.form": function(new_value) {
      if (new_value) {
        this.auto_focus_form_element();
      }
    }
  },
  mounted() {
    if (this.view == "add") {
      this.$props.step.summary = true;
    }
  }
};
</script>
