<template>
  <div class="col-cont">
    <div class="col">
      <div class="mbox" v-if="edit_view.step2.summary">
        <div style="float:right; display:flex;">
          <OnOffSlider
            style="margin-left:auto; margin-right:10px;"
            v-if="saved_form.status == 1 || saved_form.status == -1"
            :name="saved_form.id"
            v-model="saved_form.status"
            true_value="1"
            false_value="-1"
            @prop-slider-value-changed="$emit('change-service-status', $event)"
          />
          <a @click="$emit('edit-view-toggle-steps', edit_view.step2)">{{
            T__("EDIT")
          }}</a>
        </div>
        <div class="txt-h4">{{ T__("Event type details") }}</div>
        <div class="form-step-meta">
          <div style="font-size: 14px; padding-top: 10px; margin-bottom:10px;">
            {{ T__("Event type name:") }} {{ saved_form.name }}
          </div>
          <div style="margin-bottom:10px;">
            {{ T__("Invitee type:") }}
            {{ saved_form.relationship_type | relationship_type_str }}
          </div>
          <div style="padding-bottom:10px; display:flex;">
            {{ T__("Location:") }}
            <span
              style="display: flex; padding: 0 5px;"
              v-if="saved_form.locations.length"
              :inner-html.prop="saved_form.locations | locations_summary"
            ></span>
            <span
              v-if="!saved_form.locations.length"
              style="display: flex; padding: 0 5px;"
            >
              {{ T__("Not set") }}</span
            >
          </div>
          <div style="margin-bottom:10px;">
            {{ T__("Short code:") }}

            <input
              class="shortCode"
              onClick="this.select();"
              type="text"
              spellcheck="false"
              :value="'[wpcal id=' + service_id + ']'"
            />
            <img
              :src="$store.getters.get_current_admin_details.profile_picture"
              class="serviceEditViewOwnerPic"
              width="30"
              style="border-radius:50%;float:right;"
              :title="
                'This event type is available only for ' +
                  $store.getters.get_current_admin_details.display_name +
                  '.'
              "
            />
          </div>

          <div>{{ saved_form.descr }}</div>
          <hr />
          <div style="display:flex;">
            <div v-if="saved_form.post_id">
              <a
                :href="'post.php?post=' + saved_form.post_id + '&action=edit'"
                target="_blank"
                >{{ T__("Edit Booking Page") }}
                <span style="font-size:11px; font-weight:600;">&nearr;</span></a
              >
            </div>
            <div
              v-if="saved_form.post_details.link"
              style="margin-left:auto; margin-right: 12px;"
            >
              <a :href="saved_form.post_details.link" target="_blank"
                >{{ T__("View Booking Page") }}
                <span style="font-size:11px; font-weight:600;">&nearr;</span></a
              >
            </div>
          </div>
        </div>
      </div>
      <ServiceFormStep2
        v-if="edit_view.step2.form"
        :$v="$v"
        :view="view"
        :step="{ id: 2, upcoming: false, summary: false, form: true }"
        :form="form"
        :create_or_update_btn_is_enabled="create_or_update_btn_is_enabled"
        @submit-form="$emit('submit-form', $event)"
        @edit-view-toggle-steps="
          $emit('edit-view-toggle-steps', edit_view.step2)
        "
      ></ServiceFormStep2>

      <div class="mbox" v-if="edit_view.step3.summary">
        <div style="float:right">
          <a @click="$emit('edit-view-toggle-steps', edit_view.step3)">{{
            T__("EDIT")
          }}</a>
        </div>

        <div class="txt-h4">{{ T__("Event type duration &amp; timings") }}</div>
        <div class="form-step-meta mt10">
          <div style="margin-bottom: 10px;">
            {{ T__("Date range:") }}
            {{ service_date_range_summary }}
          </div>
          <div style="margin-bottom: 10px;">
            <div>
              {{ T__("Event duration:") }}
              {{ saved_form.duration | mins_to_str }}
            </div>
            <div>
              {{ T__("Show start times every:") }}
              {{ saved_form.display_start_time_every | mins_to_str }}
            </div>
          </div>

          <div style="text-decoration:underline;">
            {{ T__("Availability for this event type") }}
          </div>
          <div>
            {{ T__("Available hours:") }}
            <div style="display: inline-grid;">
              <div
                v-for="(period, index) in saved_form
                  .default_availability_details.periods"
                :key="index"
              >
                {{ period.from_time | wpcal_format_time_only(time_format) }} -
                {{ period.to_time | wpcal_format_time_only(time_format) }}
              </div>
            </div>
          </div>
          <div style="margin-bottom: 10px;">
            {{ T__("Available days:") }}
            <span>
              {{
                saved_form.default_availability_details.day_index_list
                  | wpcal_format_weekdays_summary
              }}
            </span>
          </div>
          <div style="margin-bottom: 10px;">
            {{ T__("Time zone:") }}
            <span>
              {{ saved_form.timezone | wpcal_format_tz }}
            </span>
          </div>
          <div>
            <div style="text-decoration:underline;">
              {{ T__("Advanced settings") }}
            </div>

            <div>
              {{ T__("Max bookings per day") }}:
              {{
                saved_form.max_booking_per_day
                  ? saved_form.max_booking_per_day
                  : "No limit"
              }}
            </div>
            <div>
              {{ T__("Minimum Scheduling Notice") }}:
              <span v-if="saved_form.min_schedule_notice.type == 'none'">{{
                T__("None")
              }}</span>
              <span v-if="saved_form.min_schedule_notice.type == 'units'">
                {{
                  Tsprintf(
                    /* translators: 1: int 2: mins or hours or days */
                    T__("Prevent booking of slots less than %1$s %2$s away."),
                    this.saved_form.min_schedule_notice.time_units,
                    this.saved_form.min_schedule_notice.time_units_in
                  )
                }}
              </span>
              <span
                v-if="saved_form.min_schedule_notice.type == 'time_days_before'"
              >
                {{
                  Tsprintf(
                    /* translators: 1: time HH:mm 2: int */
                    T_n(
                      "Prevent all bookings after %1$s %2$s day before.",
                      "Prevent all bookings after %1$s %2$s days before.",
                      this.saved_form.min_schedule_notice.days_before
                    ),
                    this.$options.filters.wpcal_format_time_only(
                      this.saved_form.min_schedule_notice.days_before_time,
                      time_format
                    ) + (time_format == "HH:mm" ? " " + T__("hrs") : ""),
                    this.saved_form.min_schedule_notice.days_before
                  )
                }}
              </span>
            </div>
            <div>
              {{ T__("Event buffers") }}:
              <span
                v-if="
                  !saved_form.event_buffer_before &&
                    !saved_form.event_buffer_after
                "
              >
                {{ T__("None") }}
              </span>
              <span v-if="saved_form.event_buffer_before">{{
                Tsprintf(
                  /* translators: 1: int min or int hr int min */
                  T__("%1$s before event"),
                  this.$options.filters.mins_to_str(
                    this.saved_form.event_buffer_before
                  )
                )
              }}</span>
              <span
                v-if="
                  saved_form.event_buffer_before &&
                    saved_form.event_buffer_after
                "
              >
                &amp;
              </span>
              <span v-if="saved_form.event_buffer_after">{{
                Tsprintf(
                  /* translators: 1: int min or int hr int min */
                  T__("%1$s after event"),
                  this.$options.filters.mins_to_str(
                    this.saved_form.event_buffer_after
                  )
                )
              }}</span>
            </div>
          </div>
        </div>

        <button
          style="width: 100%;"
          type="button"
          class="wpc-btn secondary md mt20"
          @click="show_service_availability_modal"
        >
          {{ T__("Set custom hours for specific days/dates") }}
        </button>
        <modal
          name="service_availability_modal"
          :width="'950px'"
          :height="'80%'"
          :adaptive="true"
          @before-close="service_availability_modal_before_close"
          style="z-index:9999;"
        >
          <ServiceAvailabilityCal
            ref="service_availability_modal_contents"
            :timezone="saved_form.timezone"
          />
        </modal>
      </div>

      <ServiceFormStep3
        v-if="edit_view.step3.form"
        :$v="$v"
        :view="view"
        :step="{ id: 3, upcoming: false, summary: false, form: true }"
        :form="form"
        :create_or_update_btn_is_enabled="create_or_update_btn_is_enabled"
        @submit-form="$emit('submit-form', $event)"
        @edit-view-toggle-steps="
          $emit('edit-view-toggle-steps', edit_view.step3)
        "
      ></ServiceFormStep3>

      <div class="mbox">
        <div
          class="form-step-meta"
          v-html="
            Tsprintf(
              /* translators: 1: html-tag-open 2: html-tag-close 3: email */
              T__(
                'New booking, rescheduling and cancellation notifications will be sent to %1$s %3$s %2$s (your WP admin email)'
              ),
              '<span style=&quot;color:#131336&quot;>',
              '</span>',
              $store.getters.get_current_admin_details.email
            )
          "
        ></div>
      </div>
    </div>

    <div class="col">
      <div
        style="font-size:16px; font-weight:500; margin: 0px 0 10px; display:flex;"
      >
        {{ T__("Additional Options") }}
        <PremiumInfoBadge style="margin: -6px 0 0 10px;">
          <em>Free: 1 question<br />Plus: Unlimited</em>
        </PremiumInfoBadge>
      </div>
      <ServiceInviteeQuestions
        v-model="form.invitee_questions"
        @save-questions="$emit('save-questions', $event)"
      />
    </div>
    <AlertAdminToUpdateProfile
      :service_details="saved_form"
      v-if="saved_form.name"
    />
  </div>
</template>

<script>
import ServiceFormStep2 from "@/components/admin/ServiceFormStep2.vue";
import ServiceFormStep3 from "@/components/admin/ServiceFormStep3.vue";
import ServiceAvailabilityCal from "@/components/admin/ServiceAvailabilityCal.vue";
import ServiceInviteeQuestions from "@/components/admin/ServiceInviteeQuestions.vue";
import AlertAdminToUpdateProfile from "@/components/admin/AlertAdminToUpdateProfile.vue";
import OnOffSlider from "@/components/form_elements/OnOffSlider.vue";

export default {
  name: "ServiceFormEditView",
  components: {
    ServiceFormStep2,
    ServiceFormStep3,
    ServiceAvailabilityCal,
    ServiceInviteeQuestions,
    AlertAdminToUpdateProfile,
    OnOffSlider
  },
  props: {
    view: {
      type: String
    },
    edit_view: {
      type: Object
    },
    form: {
      type: Object,
      required: true
    },
    saved_form: {
      type: Object
    },
    service_id: {},
    create_or_update_btn_is_enabled: {},
    $v: {}
  },
  computed: {
    service_date_range_summary() {
      let default_availability_details = this.saved_form
        .default_availability_details;
      if (default_availability_details.date_range_type === "relative") {
        let misc_value = default_availability_details.date_misc;
        let show_misc_value = misc_value.replace(/^\+((\d)*)D$/gm, function(
          x,
          y
        ) {
          return y;
        });
        return this.Tsprintf(
          /* translators: %s: int */
          this.T__("%s rolling days into the future"),
          show_misc_value
        );
      } else if (default_availability_details.date_range_type == "from_to") {
        let from_date_str = this.$options.filters.wpcal_format_std_date(
          default_availability_details.from_date
        );
        let to_date_str = this.$options.filters.wpcal_format_std_date(
          default_availability_details.to_date
        );
        return this.Tsprintf(
          /* translators: 1: date 2: date */
          this.T__("From %1$s to %2$s"),
          from_date_str,
          to_date_str
        );
      } else if (default_availability_details.date_range_type == "infinite") {
        return this.T__("Indefinitely");
      }
      return "";
    }
  },
  methods: {
    show_service_availability_modal() {
      this.$modal.show("service_availability_modal");
    },
    service_availability_modal_before_close(event) {
      let child = this.$refs.service_availability_modal_contents;
      if (child.show_edit_date_availability_popup) {
        child.close_edit_date_availability_popup_by_date();
        event.stop();
      }
    }
  }
};
</script>

<style lang="css" scoped>
.form-step-meta {
  color: #7c7d9c;
}
input[type="text"].shortCode {
  font-family: monospace !important;
  background-color: rgb(250, 250, 250);
  border: 1px solid rgba(124, 125, 156, 0.4) !important;
