<template>
  <div>
    <div>
      <div class="page-title-cont">
        <h1 class="wp-heading-inline">WPCal.io - {{ T__("Settings") }}</h1>
        <AdminHeaderRight />
      </div>
      <SettingsMenu />
      <div class="col-cont settings-cont">
        <div class="col">
          <div style="font-size: 16px;padding: 5px 0; display:flex;">
            {{ T__("Admin WPCal Usage") }}
            <PremiumInfoBadge style="margin: -4px 0px 0px 10px;">
              <em
                >Free: 1 admin user<br />Plus: 5 admin users<br />Pro: 10 admin
                users</em
              >
            </PremiumInfoBadge>
          </div>
          <table class="mbox settingAdmins">
            <tr>
              <td>{{ T__("Name") }}</td>
              <td>{{ T__("Total Event Types") }}</td>
              <td>{{ T__("Active Event Types") }}</td>
            </tr>
            <tr
              v-for="(admin_detail, index) in admin_users_details"
              :key="index"
            >
              <td>
                <a
                  :href="'user-edit.php?user_id=' + admin_detail.admin_user_id"
                  >{{ admin_detail.name }}</a
                >
              </td>
              <td>{{ admin_detail.services_count }}</td>
              <td>{{ admin_detail.services_active_count }}</td>
            </tr>
          </table>

          <AdminInfoLine info_slug="setup_other_admins" />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import SettingsMenu from "@/components/admin/SettingsMenu.vue";

import axios from "axios";

export default {
  components: {
    SettingsMenu
  },
  data() {
    return {
      document_title: "Admins ‹ Settings",
      admin_users_details: []
    };
  },
  methods: {
    load_page() {
      let wpcal_request = {};
      let action = "get_wpcal_admin_users_details";
      wpcal_request[action] = "dummy__";

      let post_data = {
        action: "wpcal_process_admin_ajax_request",
        wpcal_request: wpcal_request
      };
      axios
        .post(window.wpcal_ajax.ajax_url, post_data)
        .then(response => {
          //console.log(response);
          if (
            response.data &&
            response.data[action].hasOwnProperty("admin_users_details")
          ) {
            this.admin_users_details =
              response.data[action].admin_users_details;
          }
        })
        .catch(error => {
          console.log(error);
        });
    }
  },
  mounted() {
    this.load_page();
  }
};
</script>

<style scoped>
table.settingAdmins {
  width: 100%;
  margin: 5px 0 5px;
  border-collapse: collapse;
}
table.settingAdmins td {
  padding: 7px;
  border: 1px solid #d8dbe7;
}
table.settingAdmins tr:first-child td {
  font-family: "RubikMed" !important;
}
</style>
