<template>
  <div>
    <modal
      name="pricing_compare_modal"
      :width="'700px'"
      height="auto"
      :scrollable="true"
      :adaptive="true"
      style="z-index:9999;"
    >
      <PricingStrongerTogether />
    </modal>
  </div>
</template>

<script>
import PricingStrongerTogether from "@/components/admin/PricingStrongerTogether.vue";

import EventBus from "@/utils/event_bus.js";

export default {
  components: {
    PricingStrongerTogether
  },
  mounted() {
    EventBus.$on("show-pricing-compare-modal", payload => {
      this.$modal.show("pricing_compare_modal");
    });
  }
};
</script>
