<template>
  <div>
    <div>
      <div class="page-title-cont">
        <h1 class="wp-heading-inline">WPCal.io - {{ T__("Settings") }}</h1>
        <AdminHeaderRight />
      </div>
      <SettingsMenu />
      <div class="col-cont settings-cont">
        <div class="col">
          <div style="font-size: 16px; padding: 5px 0px;">
            {{ T__("Your Plan") }}
          </div>
          <div class="mbox" v-if="license_info.email">
            <table>
              <tr>
                <td style="text-align:right;">{{ T__("Email:") }}</td>
                <td>
                  {{ license_info.email }} (<a
                    @click="show_change_email_confirmation"
                    >{{ T__("change") }}</a
                  >)
                </td>
              </tr>
              <tr v-if="license_info.plan_name">
                <td style="text-align:right;">{{ T__("Plan:") }}</td>
                <td>
                  {{ license_info.plan_name }}
                </td>
              </tr>
            </table>
          </div>
        </div>
      </div>
    </div>
    <v-dialog />
  </div>
</template>

<script>
import SettingsMenu from "@/components/admin/SettingsMenu.vue";

export default {
  components: {
    SettingsMenu
  },
  data() {
    return {
      document_title: "Account ‹ Settings"
    };
  },
  computed: {
    license_info() {
      return this.$store.getters.get_license_info;
    }
  },
  methods: {
    show_change_email_confirmation() {
      let dialog_text =
        this.T__("This email is used only for licensing purposes") +
        ' - <ul style="list-style-type: disc;padding: revert;"><li>' +
        this.T__("Plugin activation and license validation") +
        "</li><li>" +
        this.T__("License expiry and renewal alerts") +
        "</li><li>" +
        this.T__("Plugin-related support and communication") +
        "</li></ul>";
      dialog_text += this.Tsprintf(
        /* translators: 1: html-tag-open 2: html-tag-close */
        this.T__(
          "Are you looking to change the new/reschedule/ cancellation booking notification email? %1$s Click here %2$s."
        ),
        '<a href="https://help.wpcal.io/en/article/how-to-change-booking-notification-email-address-d2k93j/" target="_blank">',
        "</a>"
      );
      dialog_text +=
        "<br><br><strong>" +
        this.T__("Are you sure you want to change the account license email?") +
        "</strong>";
      this.$modal.show("dialog", {
        title: this.T__("Alert!"),
        text: dialog_text,
        buttons: [
          {
            title: this.T__("Yes"),
            handler: () => {
              this.$router.push("/settings/account/login");
              this.$modal.hide("dialog");
            }
          },
          {
            title: this.T__("No, Cancel."),
            default: true // Will be triggered by default if 'Enter' pressed.
          }
        ]
      });
    }
  }
};
</script>
