import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

import common_module from "@/store/common_module.js";

export default new Vuex.Store({
  strict: true,
  modules: {
    common: common_module
  },
  state: {
    client_end: "admin",
    store: {
      license_info: null,
      current_admin_details: {},
      current_admin_notices: null,
      wpcal_site_urls: {}
    }
  },
  mutations: {
    set_license_info: (state, value) => {
      state.store.license_info = value;
    },
    set_store_by_obj: (state, value_obj) => {
      for (let prop in value_obj) {
        if (state.store.hasOwnProperty(prop)) {
          state.store[prop] = value_obj[prop];
        }
      }
    }
  },
  getters: {
    // get_client: state => state.client,
    // get_store: state => state.store,
    get_license_info: state => state.store.license_info,
    get_current_admin_details: state => state.store.current_admin_details,
    get_current_admin_notices: state => state.store.current_admin_notices,
    get_wpcal_site_urls: state => state.store.wpcal_site_urls
  },
  actions: {}
});
